@media screen and (max-width: 1199px) {
  body.lock {
    overflow: hidden;
  }

  .header_body {
    height: 50px;
  }

  .header_burger {
    width: 30px;
    height: 20px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header_burger:after, .header_burger:before {
    content: "";
    width: 100%;
    height: 2px;
    transition: var(--animation);
    background-color: #fff;
    display: inline-block;
    position: absolute;
    left: 0;
  }

  .header_burger:after {
    bottom: 0;
  }

  .header_burger:before {
    top: 0;
  }

  .header_burger span {
    width: 100%;
    height: 2px;
    transition: var(--animation);
    background-color: #fff;
    position: absolute;
    top: 9px;
  }

  .header_burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header_burger.active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .header_burger.active span {
    transform: scale(0);
  }

  .header_menu {
    width: 100%;
    height: 120px;
    background-color: rgba(53, 49, 49, .95);
    padding: 70px 20px 10px;
    transition: all .3s linear;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header_menu.active {
    top: 0;
  }

  .header_list {
    text-align: center;
    justify-content: center;
    display: flex;
  }

  .header_link {
    font-size: 14px;
  }

  .header_logo {
    font-size: 18px;
  }
}

